
@font-face {
  font-family: 'Formular';
  src: url(/static/fonts/Formular-Regular.48b6546.otf);
  font-weight: 400;
}
@font-face {
  font-family: 'Formular';
  src: url(/static/fonts/Formular-Medium.886ca31.otf);
  font-weight: 600;
}
@font-face {
  font-family: 'Reforma2018';
  src: url(/static/fonts/Reforma2018-Gris.c4ac2b9.ttf);
  font-weight: 400;
}
@font-face {
  font-family: 'Reforma2018';
  src: url(/static/fonts/Reforma2018-Negra.81567ba.ttf);
  font-weight: 700;
}
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#app {
    /* css variables */
    --bgColor: #2E364A;
    --inputFocusColor: #232834;
    --inputPlaceholderColor: #030814;

    font-family: 'Formular', 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-weight: 400;
    text-align: center;
    color: white;
    width: 100%;
    background: var(--bgColor);
    /* margin-top: 60px; */
}
.container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 1280px;
    margin: 0 auto;
    padding-top: 30px;
}
.logo-heading {
    font-family: 'Reforma2018';
    width: 100%;
    position: relative;
    text-transform: uppercase;
    font-size: 25px;
    line-height: 1.4;
    font-weight: 400;
}
.logo-number {
    font-family: 'Reforma2018';
    display: block;
    position: absolute;
    top: -18px;
    left: -20px;
    font-size: 38px;
    line-height: 1.4;
    font-weight: 700;
}
.table {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.table__head-row,
  .table__player-row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    width: 1190px;
    height: 64px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
}
.table__player-row {
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.table__player-row:nth-child(odd) {
    background-color: rgba(255, 255, 255, 0.1);
}
.table__head {
    width: 86px;
    font-size: 20px;
    line-height: 1.2;
}
.table__player {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 86px;
    height: 100%;
}
.table__player--name,
  .table__head--name {
    width: 160px;
}
.dialog {
    position: fixed;
    /* display: flex; */
    /* flex-direction: column; */
    width: 500px;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    /* text-align: center; */
    /* color: #fff; */
    /* background-color: var(--inputPlaceholderColor); */
}
.dialog__header {
    height: 120px;
    background-color: var(--inputPlaceholderColor);
    /* background-color: #4C5772; */
}
.dialog__heading {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #fff;
    font-size: 30px;
    line-height: 120px;
    font-weight: 600;
    text-transform: uppercase;
}
.dialog__heading::before,
  .dialog__heading::after {
    content: '';
    width: 46px;
    height: 53px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
}
.dialog__results {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 30px 0;
    /* background-color: #4C5772; */
    background-color: var(--bgColor);
    text-align: center;
}
.dialog__results-list {
    margin-bottom: 30px;
    list-style: none;
    color: #fff;
    font-size: 20px;
}
li:first-of-type {
    color: red;
    font-weight: 600;
    font-size: 40px;
    margin: 10px 0;
}
input {
    width: 80%;
    height: 24px;
    color: #fff;
    font-size: 20px;
    line-height: 1.2;
    background-color: var(--bgColor);
    border: none;
    -webkit-transition: 0.2s all linear;
    transition: 0.2s all linear;
}
input:placeholder-shown {
    background-color: var(--inputPlaceholderColor);
}

/* .table__player-row:nth-child(odd) > input {
    background-color: rgba(255, 255, 255, 0.1);
  } */
.table__player-row:nth-child(odd) input:placeholder-shown {
    background-color: var(--inputPlaceholderColor);
}
.table__player-row:nth-child(odd) input:focus {
    background-color: var(--inputFocusColor);
}
.table__player-row:nth-child(odd) input {
    background-color: transparent;
}
input:focus {
    width: 100%;
    height: 100%;
    font-size: 35px;
    line-height: 1.2;
    background-color: var(--inputFocusColor);
    outline: none;
}
input::-webkit-inner-spin-button {
    display: none;
}
.buttons-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin: 45px 0;
    width: 100%;
}
.button {
    border-radius: 50px;
    font-size: 20px;
    line-height: 1.2;
    color: #fff;
    background-color: transparent;
    border: 2px solid #fff;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    outline: none;
}
.button:hover {
    color: var(--bgColor);
    background-color: #fff;
    /* border: 2px solid #fff; */
}
.button:disabled {
    color: rgba(255, 255, 255, 0.3);
    border: 2px solid rgba(255, 255, 255, 0.3);
}
.button--add {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-left: 50px;
    border: none;
    width: 120px;
    color: #96979A;
}
.button--add::before {
    content: '+';
    font-size: 30px;
    font-weight: 600;
}
.button--add:hover {
    color: #fff;
    background-color: transparent;
}
.button--add:disabled {
    border: none;
}
.button--add:disabled:hover {
    color: #96979A;
}
.button--score {
    width: 140px;
    height: 43px;
    /* margin: 0 20px; */
}
.button--close {
    /* position: absolute;
    right: 10px;
    top: 10px; */
    display: block;
    width: 140px;
    height: 75px;
    font-weight: 600;
    cursor: pointer;
    background-color: #353e55;
}
.button--close:hover {
    border: 2px solid var(--bgColor);
}

/* animations */
.slide-fade-enter-active {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
.slide-fade-leave-active {
    -webkit-transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
    /* transform: translateX(10px); */
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
    opacity: 0;
}
.list-enter-active, .list-leave-active {
    -webkit-transition: all 1s;
    transition: all 1s;
}
.list-enter, .list-leave-to {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}

